import { React, Fragment, useState, useLayoutEffect } from 'react';
import { Redirect } from 'react-router-dom';
import NotFound from '../../Auth/NotFound/NotFound';
import MaterialTable from 'material-table';
import Button from '@material-ui/core/Button';
import Spinner from '../../../components/UI/Spinner/Spinner';
import SnackBar from '../../../components/UI/SnackBar/SnackBar';
import axios from 'axios';

const styles = {
  maxWidth: '90%',
  margin: '1em auto',
};

const AttendancePartaker = (props) => {
  const [snackStatus, setSnackStatus] = useState({
    openSnackBar: false,
    snackBarMessage: '',
    severity: '',
  });
  const [auxRegister, setAuxRegister] = useState(false);
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);
  const [course, setCourse] = useState('');
  const [notFound, setNotFound] = useState(false);

  useLayoutEffect(() => {
    request();
    // eslint-disable-next-line
  }, [auxRegister]);

  const request = () => {
    setLoading((loading) => !loading);
    //let url ='/api/attendance-partaker/'
    axios
      .get(
        '/api/attendance-partaker/' + props.match.params.group
      )
      .then((response) => {
        setData(response.data);
        setCourse(response.data[0].course);
        setLoading((loading) => !loading);
      })
      .catch((error) => {
        setLoading((loading) => !loading);
        if (error.response) {
          //Redirige si el usuario no esta inscrito a cierto grupo o si el grupo no existe
          if (error.response.data.message == 'Pagina no encontrada') {
            setNotFound(true);
          } else {
            //Se realizo la peticion y el servidor respondio con un codigo de status
            window.alert('Error: ' + error.response.data.message);
          }
        } else if (error.request) {
          //Se realizo la peticion pero no se recibio una respuesta
          window.alert('Error: ' + error.request.data.message);
        } else {
          //Algo ocurrio al preparar la peticion que desencadeno un error
          console.log('Error: ' + error.message);
        }
        console.log(error.config);
      });
  };

  const registerAttendance = (e, todaysDate) => {
    e.preventDefault();
    setLoading((loading) => !loading);

    //let url = '/auth/attendance-partaker/' + props.match.params.group, {todaysDate}
    axios
      .post(
        '/auth/attendance-partaker/' + props.match.params.group, {todaysDate}
        )
      .then((response) => {
        setLoading((loading) => !loading);
        setAuxRegister((auxRegister) => !auxRegister);
        setSnackStatus({
          openSnackBar: true,
          severity: 'success',
          snackBarMessage: 'Asistencia registrada correctamente',
        });
      })
      .catch((error) => {
        setLoading((loading) => !loading);
        if (error.response) {
          //Se realizo la peticion y el servidor respondio con un codigo de status
          setSnackStatus({
            openSnackBar: true,
            severity: 'error',
            snackBarMessage: error.response.data.message,
          });
        } else if (error.request) {
          //Se realizo la peticion pero no se recibio una respuesta
          window.alert('Error: ' + error.request.data.message);
        } else {
          //Algo ocurrio al preparar la peticion que desencadeno un error
          console.log('Error: ' + error.message);
        }
        console.log(error.config);
      });
  };

  let courseEnrollmentTable = (
    <Fragment>
      <MaterialTable
        title={'Asistenicia: ' + course}
        columns={[
          { title: 'Fecha(d/m/a)', field: 'date' },
          { title: 'Hora', field: 'schedule' },
          { title: 'Estatus', field: 'status' },
          { title: 'Active', field: 'active', hidden: true },
        ]}
        data={data}
        actions={[
          {
            icon: 'save',
            tooltip: 'Save User',
            onClick: (event, rowData) => {
              registerAttendance(event, rowData.date);
            },
          },
        ]}
        components={{
          Action: (props) => (
            <Button
              disabled={props.data.active}
              onClick={(event) => props.action.onClick(event, props.data)}
              color='primary'
              variant='contained'
              style={{ textTransform: 'none' }}
              size='small'
            >
              Registrar asistencia
            </Button>
          ),
        }}
        options={{
          search: false,
          actionsColumnIndex: -1,
          pageSize: 6,
          pageSizeOptions: [6],
          headerStyle: {
            backgroundColor: '#e4a010',
            color: '#FFF',
            fontSize: '15px',
            fontWeight: 'bold',
          },
          filterCellStyle: {
            backgroundColor: '#01579b',
            color: '#FFF',
          },
          searchFieldStyle: {
            backgroundColor: '#E8E8E8',
            color: 'black',
          },
        }}
        localization={{
          header: {
            actions: 'Registrar asistencia',
          },
          toolbar: {
            searchTooltip: 'Buscar',
            searchPlaceholder: 'Buscar',
          },
          pagination: {
            labelDisplayedRows: '{from}-{to} de {count}',
            labelRowsSelect: 'Filas',
            firstAriaLabel: 'Primera pagina',
            firstTooltip: 'Primera pagina',
            previousAriaLabel: 'Pagina anterior',
            previousTooltip: 'Pagina anterior',
            nextAriaLabel: 'Siguiente pagina',
            nextTooltip: 'Siguiente pagina',
            lastAriaLabel: 'Ultima pagina',
            lastTooltip: 'Ultima pagina',
          },
          body: {
            emptyDataSourceMessage: 'No se ha inscrito a un curso',
          },
        }}
      />
    </Fragment>
  );

  if (loading) {
    courseEnrollmentTable = <Spinner />;
  }

  if (notFound) {
    courseEnrollmentTable = <Redirect to='/notFound' component={NotFound} />;
  }

  return (
    <div style={styles}>
      {courseEnrollmentTable}
      <SnackBar
        openSnackBar={snackStatus.openSnackBar}
        closeSnackBar={() => setSnackStatus({ openSnackBar: false })}
        snackBarMessage={snackStatus.snackBarMessage}
        severity={snackStatus.severity}
      />
    </div>
  );
};

export default AttendancePartaker;
